/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: #0da6f3 #1a1b15;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 0.7rem;
}

*::-webkit-scrollbar-track {
  background: #1a1b15;
}

*::-webkit-scrollbar-thumb {
  background-color: #0da6f3;
  border-radius: 10px;
  border: 1px solid #0da6f3;
}

input[type=number] {
  appearance: none;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
  margin: 0;
}

.nombre-pagina {
  text-align: center;
  margin: 0rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
  margin-bottom: 1rem;
}

.contenedor-app {
  width: 100%;
  height: 100vh;
  align-items: center;
  box-sizing: content-box;
}
.contenedor-app .imagen {
  height: 15rem;
  overflow: hidden;
}
.contenedor-app .imagen img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  margin: none;
}
.contenedor-app .appContainer {
  position: relative;
  height: 90%;
  overflow-y: auto;
}
.contenedor-app .appContainer .seccion_titulo {
  margin: 0.5rem 0;
}
.contenedor-app .appContainer .seccion_descripcion {
  margin: 0.5rem 0;
}
.contenedor-app .appContainer .form {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(1, 1fr);
  }
  .contenedor-app .imagen {
    position: absolute;
    z-index: -1;
    top: 0;
    width: 100%;
    height: 100vh;
    opacity: 0.5;
  }
  .contenedor-app .appContainer {
    width: 80%;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }
  .contenedor-app .imagen {
    position: inherit;
    height: 100%;
    opacity: 1;
  }
  .contenedor-app .appContainer {
    width: 90%;
  }
}

.barra {
  display: flex;
  justify-content: space-between;
  padding: 0 4rem;
  margin: 1rem;
}
.barra .saludo {
  font-weight: bold;
}
.barra .cerrarSesion {
  color: #FFFFFF;
  font-size: 2rem;
}

.ocultar {
  display: none;
}

.mostrar {
  display: block;
}

.seccion .text-center {
  text-align: center;
}

.listado-servicios {
  display: grid;
  gap: 0rem;
  margin: 0 auto;
  padding: 0 2rem;
  height: 30rem;
  overflow-y: scroll;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
.listado-servicios::-webkit-scrollbar-thumb {
  width: 0rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
    overflow: visible;
    height: auto;
  }
}
@media (min-width: 1024px) {
  .listado-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
    width: 90%;
  }
}
.listado-servicios .servicio {
  border-radius: 1rem;
  background-color: #FFFFFF;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition-property: transform;
  transition-duration: 0.3s;
  position: relative;
}
.listado-servicios .servicio:hover {
  transform: scale(1.03);
}
.listado-servicios .servicio.azul {
  background-color: #0da6f3;
}
.listado-servicios .servicio.azul p {
  color: #FFFFFF;
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0;
  text-align: center;
}
.listado-servicios .servicio .nombre-servicio {
  flex-basis: 60%;
  font-size: 1.8rem;
}
.listado-servicios .servicio .precio-servicio {
  flex-basis: 40%;
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.5rem;
}
.listado-servicios .servicio .botonesBox {
  width: 60%;
  margin: 0 auto;
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  text-align: center;
  gap: 2rem;
}
.listado-servicios .servicio .botonesBox .fa-plus,
.listado-servicios .servicio .botonesBox .fa-minus {
  font-weight: bold;
  font-size: 3rem;
  cursor: pointer;
}
.listado-servicios .servicio .botonesBox .fa-plus {
  color: #329f00;
}
.listado-servicios .servicio .botonesBox .fa-minus {
  color: #cb0000;
}
.listado-servicios .servicio .servicio-opciones {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 0 auto;
  margin-top: 1rem;
}
.listado-servicios .servicio .servicio-opciones .icon {
  font-weight: bold;
  font-size: 2rem;
}
.listado-servicios .servicio .servicio-opciones .icon i {
  transition: color 0.3s ease;
}
.listado-servicios .servicio .servicio-opciones .actualizar {
  color: #e1e1e1;
}
.listado-servicios .servicio .servicio-opciones .eliminar {
  color: #e1e1e1;
}
.listado-servicios .servicio:hover > .servicio-opciones .actualizar {
  color: #329f00;
}
.listado-servicios .servicio:hover > .servicio-opciones .eliminar {
  color: #cb0000;
}

.resumen {
  width: 90%;
  margin: 0 auto;
}
.resumen p {
  text-align: center;
}
.resumen p span {
  font-weight: bold;
}
.resumen .boton {
  margin: 2rem 0;
}
.resumen .listado-servicios {
  height: 100%;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  overflow: visible;
  flex-wrap: wrap;
}

.citas {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .citas {
    height: 50rem;
  }
}

.cita {
  width: 100%;
  background-color: #2f2f2f;
  padding: 1rem 0;
  border-radius: 1rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.cita h3 {
  margin: 0.5rem 0;
  font-size: 2.5rem;
}
.cita h4 {
  margin: 0.5rem 0;
}
.cita .info {
  background-color: #626262;
  width: 100%;
  text-align: center;
  padding: 1rem 0;
}
.cita .info .dato {
  font-weight: bold;
}
.cita .info .dato span {
  font-weight: lighter;
}
.cita .total {
  width: 90%;
  padding: 1rem;
  text-align: right;
}
.cita .total p {
  font-weight: bold;
  margin: 0;
}
.cita .listado-servicios {
  height: 15rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  overflow-y: auto;
}
.cita .form-eliminar {
  width: 90%;
  text-align: right;
  margin: 1rem;
}
.cita .form-eliminar .boton-eliminar {
  font-weight: bold;
  background-color: #cb0000;
  color: #FFFFFF;
  outline: none;
  border: none;
  margin: 0;
  padding: 1rem;
  border-radius: 1rem;
  cursor: pointer;
}

.barra-servicios {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  width: 90%;
  margin: 1rem auto;
}
.barra-servicios .servicio {
  padding: 0.7rem;
  border-radius: 1rem;
  font-weight: bold;
  background-color: #0da6f3;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form {
  width: 100%;
  margin-top: 4rem;
}

.form .campo {
  display: flex;
  margin: 2rem 0;
  align-items: center;
  width: 100%;
  height: 4.1rem;
  overflow: hidden;
  border-radius: 1rem;
}
.form .campo label {
  display: flex;
  align-items: center;
  height: 100%;
  background-color: #626262;
  font-weight: 700;
  padding-left: 1.5rem;
  flex: 0 0 15rem;
}
.form .campo input {
  flex: 1;
  height: 100%;
  outline: none;
  border: none;
  padding: 0 1rem;
  font-weight: 700;
}
.form .campo input:disabled {
  background: #FFFFFF;
  cursor: not-allowed;
}
.form .campo .icon {
  flex: 0 0 4rem;
  height: 100%;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #3b3b3b;
  cursor: pointer;
}

.boxButton {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.boton {
  background-color: #0da6f3;
  color: #FFFFFF;
  font-weight: bold;
  padding: 2rem 2rem;
  border-radius: 1rem;
  text-align: center;
  font-size: 2rem;
  border: none;
  outline: none;
  cursor: pointer;
}

.acciones {
  margin-top: 4rem;
  display: flex;
  width: 100%;
  gap: 2rem;
}
.acciones .accion {
  background-color: #0a85c3;
  width: fit-content;
  padding: 1rem 2rem;
  border-radius: 1rem;
}
.acciones .accion a {
  color: #FFFFFF;
}

.alert {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.alert .err, .alert .enviado {
  padding: 0.7rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: bold;
  width: 100%;
  text-align: center;
}
.alert .err {
  background-color: #cb0000;
}
.alert .enviado {
  background-color: #329f00;
}

.tabs {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 1rem;
}
.tabs button {
  text-align: center;
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 0.5rem;
  display: block;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
  border-radius: 0.7rem;
  transition: background-color 0.3s;
}
.tabs button:hover {
  background-color: #076493;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.app .paginacion {
  width: 90%;
  margin: 0 auto;
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 1024px) {
  .app .paginacion {
    width: 100%;
  }
}